import { useState, useEffect } from 'react';
import { sendLoginRequest } from '../api/LoginApi';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

function Login({ isLoggedIn, setIsLoggedIn, setUserName, userName, setUserId}) {
  const [id, setId] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();
  const loginPHP = 'http://leedh9276.dothome.co.kr/test/verify.php';

  async function verifyToken() {
    const token = localStorage.getItem('token');
    const response = await axios(loginPHP, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      },
      withCredentials: true
    });
    const data = await response.data;
    if (data.success === true) {
      setIsLoggedIn(true);
      alert('이미 로그인 되었습니다.');
    }
  }
  
  useEffect(() => {
    verifyToken();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);


  // 로그인 버튼 클릭 시
  const onSubmit = async (event) => {
    event.preventDefault();
    try {
      const data = await sendLoginRequest(id, password);
      setIsLoggedIn(true);

      localStorage.setItem('token', data.token);

      console.log(data);
      if (data.success === true) {
        setUserId(data.user_id);
        setUserName(data.user_name);
        navigate('/');
      }

    } catch (error) {
      console.error(error);
      setError(error.message);
    }
  };

  return (
    <div className='login'>
      <form onSubmit={onSubmit}>
        <label htmlFor="Id">
          enter your id
        </label>
        <input type="text" id="id" name="id" value={id} onChange={(event) => setId(event.target.value)} />
        <label htmlFor="password">
          enter password
        </label>
        <input type="password" id="password" name="password" value={password} onChange={(event) => setPassword(event.target.value)} />

        <button type="submit">Submit!</button>
      </form>

      {isLoggedIn && <div>{userName}님 환영합니다.</div>
      }

      {error && <div>{error}</div>}
    </div>
  );
}

export default Login;
