import React, {useState, useEffect} from 'react';
import axios from 'axios';
import NewProduct from '../List/MujiNewProduct';
import './Main.css';
import MainBanner from '../List/MainBanner';
import MujiSales from '../List/MujiSales';
import MujiWalker from '../List/MujiWalker';
import MujiMagazine from '../List/MujiMagazine';
import MujiMDPick from '../List/MujiMDPick';
import MujiWeather from '../List/MujiWeather';
import MujiShopInfo from '../List/MujiShopInfo';

function Index(props) {

  const [list, setList] = useState([]);
  const [error, setError] = useState(null);

  const [categoryBanner, setCategoryBanner] = useState('');
  const [categoryNew, setCategoryNew] = useState('');
  const [categorySales, setCategorySales] = useState('');
  const [categoryWalker, setCategoryWalker] = useState('');



  const productListPHP = 'http://leedh9276.dothome.co.kr/test/Product_chk.php';

  const fetchList = async() => {
    try { // 응답 성공
      const response = await axios.get(productListPHP);
      setList(response.data.list);
    } catch (e) { // 응답 실패
      setError(e);
      console.log(e +error);
    }
  }

  useEffect(() => {
    fetchList();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // 각 JSON 필터
  const categoryNewList = list.filter(item => item.cate === '가구');
  const categoryWalkerList = list.filter(item => item.cate === 'MUJI WALKER'); 

  return (
    <div>
      <MainBanner/>
      <NewProduct list={categoryNewList} />
      <MujiSales list={list}/>
      <MujiWalker list={categoryWalkerList} />
      <MujiMagazine />
      <MujiMDPick list={list} />
      <MujiWeather list={list} />
      <MujiShopInfo />
    </div>
  );
}

export default Index;