import React, { useState, useEffect } from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import axios from 'axios';
import Index from './Index/Index';
import Login from './Login/Login';
import Header from './Header/Header';
import NotFound from './Index/NotFound';
import Product from './List/Product';
import Sign from './Login/Sign';
import './App.css';



function App(props) {
  const[userName, setUserName] = useState(null);
  const[userId, setUserId] = useState(null);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const tokenChek = 'http://leedh9276.dothome.co.kr/test/verify.php';

  useEffect(() => {
    verifyUser();
  }, []);

  const verifyUser = async () => {
    const token = localStorage.getItem('token');
    try {
      const response = await axios(tokenChek, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        withCredentials: true
      });
      if (response.data) {
        setUserName(response.data.user_name);
        setUserId(response.data.user_id);
        setIsLoggedIn(true);
      }
    } catch (err) {
      console.log(err);
      setIsLoggedIn(false);
      setUserId('');
      setUserName('');
    }
  }

  const logOut = () => {
    localStorage.removeItem('token');
    setIsLoggedIn(false);
    setUserId('');
    setUserName('');
  };

  return (
    <BrowserRouter basename="/muji">
        <Header userName={userName} logOut={logOut} isLoggedIn={isLoggedIn} userId={userId}/>
      <Routes>
        <Route exact path="/" element={<Index />} />
        <Route path="/login" element={<Login isLoggedIn={isLoggedIn} setIsLoggedIn={setIsLoggedIn} userName={userName} setUserName={setUserName} setUserId={setUserId} userId={userId} />} />
        <Route path="/signup" element={<Sign />} />
        <Route path="/product" element={<Product />} />
        <Route path="/*" element={<NotFound />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
