import React, {useRef} from 'react';
import { Link } from 'react-router-dom';
import './css/MujiMDPick.css';

// swiper module
import {Swiper, SwiperSlide} from 'swiper/react';
import SwiperCore, {Pagination, Navigation} from "swiper";

import "swiper/css";
import "swiper/css/pagination";

function MujiMDPick(props) {

  SwiperCore.use([Navigation]);

  const list = props.list;
  const prevRef = useRef(null);
  const nextRef = useRef(null);


  return (
    <article className='muji_mdpick'>
      <p className='main-info'>
        <span>MUJI PRODUCT</span>
      </p>
      <h2 className='main-title'>MD's PICK</h2>
      <Swiper
        slidesPerView={6}
        spaceBetween={30}
        loop={true}
        pagination={{ clickable: false }}
        navigation={{ 
          prevEl: prevRef.current, 
          nextEl: nextRef.current
        }} 
        modules={[Pagination, Navigation]}
        className="mySwiper"
      >
        {list.slice(0, 12).map(item => (
          <SwiperSlide key={item.no} className='muji_mdpick-slide'>
            <Link 
              to={`/product?id=${item.no}`} 
              className='muji_mdpick-slide-item'
            >
              <img src={`http://leedh9276.dothome.co.kr/test/productimg/${item.img}`} alt={item.name} className='muji_mdpickslide-item__img'/>
              <p className='main-item-title'>{item.name}</p>
              <p className='main-item-price'>{item.price}</p>
            </Link>
          </SwiperSlide>
        ))}
      </Swiper>

      <div className="swiper-button-prev" ref={prevRef}> 
          <img src={`${process.env.PUBLIC_URL}/images/slide-prev.svg`} alt="이전" />
      </div>
      <div className="swiper-button-next" ref={nextRef}>
          <img src={`${process.env.PUBLIC_URL}/images/slide-next.svg`} alt="다음" />
      </div>
    </article>
  );
}

export default MujiMDPick;