import React, {useState, useEffect} from 'react';
import { useNavigate, Link } from 'react-router-dom';
import LNB_wear from './LNB_wear';
import LNB_Funiture from './LNB_Funiture';
import './Header_sub.css';
import LNB_Other from './LNB_Other';

function HeaderSub({setToggleState, toggleState, userName, logOut, toggle}) {

  const [headerSub, setHeaderSub] = useState('header-sub');
  const [act, setAct] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    if (toggleState === 1) {
      setHeaderSub('header-sub active');
      setAct('Active');
    } else {
      setHeaderSub('header-sub');
      setAct('');
    }
  }, [toggleState]);

  const login = () => {
    setToggleState(0);
    navigate('/login');
  }
  const logOutBtn = () => {
    setToggleState(0);
    logOut();
  }

  const register = () => {
    console.log('회원가입');
    setToggleState(0);
    navigate('/signup');
  }

  return (
    <div className={headerSub}>
      <div className="header-sub-wrap">
        <LNB_Funiture />

        <div>
          {userName ? (
            <div className='profile-wrap'>
              <img src="http://leedh9276.dothome.co.kr/test/img/profile/profile.png" alt="비회원 로그인" />
              <p className='profile-info'>
                <span>환영합니다 <br />
                <strong>{userName}님.</strong> 
                </span>
                <button onClick={logOutBtn} className='profile-btn'>로그아웃</button>
              </p>
              <p className='my-profile'>
                <span>마이페이지</span>
                <span>구매내역</span>
                <span>배송조회</span>
              </p>
            </div>
          ) : (
            <div className='profile-wrap'>
              <img src="http://leedh9276.dothome.co.kr/test/img/profile/defaultprofile.png" alt="비회원 로그인" />
              <p className='profile-info'>
                <span>로그인 되어 있지 않습니다. <br />
                <strong>로그인</strong> 해주세요.
                </span>
              </p>
              <p className='profile-info'>
                <button onClick={login} className='login-btn'>로그인</button>
                <button onClick={register} className='join-btn'>회원가입</button>
              </p>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default HeaderSub;