import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import './Hbar.css';
import GNB from './GNB';

function HeaderBar({toggle, toggleState}) {

  const [act, setAct] = useState('');


  useEffect(() => {
    if (toggleState === 1) {
      //Hbar.ToggleSwitch에 Active 클래스 추가
      setAct('Active');
    } else {
      //Hbar.ToggleSwitch에 Active 클래스 제거
      setAct('');
    }
  }, [toggleState]);

  
  return (
    <div className="HeaderWrap">
      <h1>
        <Link to="/">
          <img src={`${process.env.PUBLIC_URL}/images/logo-black.svg`} alt="" />
        </Link>
      </h1>

      <div className='HeaderBtn-wrap'>
        <GNB />
        <button type="button" className={`ToggleSwitch ${act}`} onClick={toggle}>
          <span>&nbsp;</span>
          <span>&nbsp;</span>
        </button>
      </div>
      </div>
  );
}

export default HeaderBar;
