import React, {useState, useEffect} from 'react';
import axios from 'axios'; // modify the import statement
import { useLocation } from 'react-router-dom';
import { Link } from 'react-router-dom';
import './css/MujiProduct.css';


function Product(props) {

  const [error, setError] = useState(null);

  //상품정보 state
  const [productID, setProductID] = useState('');
  const [productName, setProductName] = useState('');
  const [productPrice, setProductPrice] = useState('');
  const [productImage, setProductImage] = useState('');
  const [detailImage, setDetailImage] = useState('');

  // 주소창 ?id를 통해 받아온 id값을 변수에 저장
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const id = params.get('id');

  const productListPHP = 'http://leedh9276.dothome.co.kr/test/Product_list.php';

  // useEffect로 컴포넌트가 처음 렌더링될 때만 실행되도록 설정
  useEffect(() => {
    fetchList();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetchList = async() => {
    try {
      const response = await axios.get(`${productListPHP}?id=${id}`);
      setProductID(response.data.no);
      setProductName(response.data.name);
      setProductPrice(response.data.price);
      setProductImage(response.data.img);
      setDetailImage(response.data.detail_imgs);
      console.log(response.data);
    } catch (e) {
      setError(e);
      console.log(e + error);
    }
  }

  return (
    <div className='list-wrap'>
      <h2>상품페이지</h2>
      <ul>
        <li>상품번호: {productID}</li>
        <li>상품명: {productName}</li>
        <li>상품가격: {productPrice}</li>
        <li>상품이미지: {productImage}</li>
      </ul>
      <Link to = '/'>
        <button>목록으로</button>
      </Link>

      <div className="detail">
        <img src={`http://leedh9276.dothome.co.kr/test/productimg/${detailImage[0]}`} alt="상세1" />
        <img src={`http://leedh9276.dothome.co.kr/test/productimg/${detailImage[1]}`} alt="상세1" />
        <img src={`http://leedh9276.dothome.co.kr/test/productimg/${detailImage[2]}`} alt="상세1" />
        <img src={`http://leedh9276.dothome.co.kr/test/productimg/${detailImage[3]}`} alt="상세1" />
        <img src={`http://leedh9276.dothome.co.kr/test/productimg/${detailImage[4]}`} alt="상세1" />
        <img src={`http://leedh9276.dothome.co.kr/test/productimg/${detailImage[5]}`} alt="상세1" />
      </div>
    </div>
  );
}

export default Product;