import React, {useState} from 'react';
import './GNB.css';

function GNB(props) {

  const [searchbox, setSearchbox] = useState(false);
  const searchboxEvent = () => {
    if (searchbox === false) {
      setSearchbox(true);
    } else {
      setSearchbox(false);
    }
  }

  return (
    <nav className='GNB'>
      <ul>
        <li>인기상품</li>
        <li>주간특가</li>
        <li>의류 &middot; 패션</li>
        <li>생활용품</li>
        <li>식품</li>
        <li onClick={searchboxEvent}>
          <img src={`${process.env.PUBLIC_URL}/images/search.svg`} alt="검색" />
        </li>
        <li>
          <img src={`${process.env.PUBLIC_URL}/images/cart.svg`} alt="장바구니" />
        </li>
      </ul>
    </nav>
  );
}

export default GNB;